<template>
  <div>
    <div style="margin: 15px">
      <Button type="info" @click="addItem">新增</Button>
      <Button type="info" @click="updateItem">修改</Button>
      <Button type="warning" @click="deleteItems">删除</Button>
      <slot name="toolbar"></slot>
    </div>
    <Table border :columns="tableMeta" :data="tableData" @on-selection-change="itemSelected"></Table>
    <form-dialog v-if="openFlag"
                 :formData="formData"
                 :formMeta="formMeta"
                 :showDialog.sync="showDialog"
                 v-on:handleItemChange="handleItemChange"></form-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  import DataCenter from "../store/DataCenter.js";
  import formDialog from "./FormDialog.vue";
  import {DataSource} from "../utils/dataSource.js";

  export default {
    props: ['pageName'],
    components: {formDialog},
    data: function () {
      return {
        tableMeta: [],
        tableData: [],
        selectedItems: [],
        openFlag: false,
        showDialog: false,
        formMeta: [],
        formData: null
      }
    },
    mounted: function() {
      //1、获取页面配置信息
      let config = DataCenter.getPageConfig(this.pageName).table;
      let meta = this._preHandleMeta(config.fieldMeta);
      this.tableMeta = meta.table;
      this.formMeta = meta.form;

      //2、创建datasource
      this.ds = DataCenter.getDataSource(config.bind);
      this._refreshTable();
    },
    methods: {
      _preHandleMeta: function(fieldMeta) {
        var res = {
          table:[{
            type: 'selection',
            width: 60,
            align: 'center'
          }],
          form: []
        };
        if(fieldMeta) {
          for(let i= 0,len=fieldMeta.length; i<len; i++) {
            let item = fieldMeta[i];
            if(false != item.showInTable) {
              var column = {};
              column.title = item.label;
              column.key = item.name;
              if("q-select" == item.xType) {
                column.key = item.name+"$display$";
              }

              res.table.push(column);
            }
            if(false != item.showInForm) {
              res.form.push(item);
              //给表单的数据初始值，用于新增
              if(!this.initFormData) {
                this.initFormData = {};
              }
              this.initFormData[item.name] = "";
            }
          }
        }
        return res;
      },
      _refreshTable: async function() {
        let data = await DataCenter.getPagedByDS(this.ds);
        if(data.success) {
          this.tableData = data.result;
        }
      },
      addItem: function() {
        this.operType = "add";
        this.formData = Object.assign({}, this.initFormData);//传入object，同样会被子组件影响
        this.openFlag = true;
        this.showDialog = true;
      },
      updateItem: function() {
        if(0==this.selectedItems.length) {
          this.$Modal.warning({content: "请至少选中一项！"});
        }else {
          this.operType = "update";
          this.formData = this.selectedItems[0];
          this.openFlag = true;
          this.showDialog = true;
        }
      },
      deleteItems: function() {
        let len = this.selectedItems.length;
        if(0==len) {
          this.$Modal.warning({content: "请至少选中一项！"});
        }else {
          let that = this;
          this.$Modal.confirm({
            content: "确定删除所选项吗？",
            onOk: function() {
              that.operType = "delete";
              let param = {ids: []};
              for(let i= 0; i<len; i++) {
                param.ids.push((that.selectedItems[i]).id);
              }
               that.handleItemChange(param);
            }
          })
        }
      },
      itemSelected: function(selection, row) {
        this.selectedItems = selection;
        this.$emit("itemSelectEvent", selection);
      },
      handleItemChange: async function(param, dialogVM) {
        let res = false;
        let response;

        //自动增加appId
        let appId = DataCenter.getAppId();
        if(!appId && this.pageName!="appMgr") {
          this.$Modal.error({content: "未选中项目!"});
        }else {
          if("add" == this.operType) {
            if(this.pageName!="appMgr") {
              param.appId = appId;
            }

            response = await this.ds.create(param);
          }else if("update" == this.operType) {
            response = await this.ds.update(param, {id: param.id});
          }else if("delete" == this.operType) {

            response = await this.ds.remove(param);
          }
          let data = await response.json();
          res = data.success;
          if(res) {
            this.$Modal.success({content: "操作成功!"});
            this.showDialog = false;
            this._refreshTable();
          }else {
            this.$Modal.error({content: "操作失败!"});
          }
        }
      }
    }


  }
</script>



