<template>
  <Modal
          v-model="open"
          title="编辑"
          :mask-closable="false"
          @on-cancel="cancel">

    <Form :model="formDatas" :label-width="80">
      <Form-item v-for="item in formMeta" :label="item.label" :key="item.name">
        <Input v-if="'q-input'==item.xType" v-model="formDatas[item.name]" placeholder="请输入"></Input>
        <Date-picker v-if="'q-date'==item.xType" type="datetime" format="yyyy-MM-dd HH:mm" placeholder="选择日期" v-model="formDatas[item.name]"></Date-picker>
        <Select v-if="'q-select'==item.xType" v-model="formDatas[item.name]" placeholder="请选择">
          <Option v-for="option in selectDatas[item.name]"
                  :value="option[item.valueKey || 'id']"
                  :key="item.valueKey || 'id'">{{option[item.textKey || 'name']}}</Option>
        </Select>
      </Form-item>
    </Form>

    <div slot="footer">
      <i-button type="text" @click="cancel">取消</i-button>
      <i-button type="primary" @click="handleSave">保存</i-button>
    </div>

  </Modal>
</template>

<script>
  import {DataSource} from "../utils/dataSource.js";
  import DataCenter from "../store/DataCenter.js";


  export default {
  props: ['showDialog', 'formData', 'formMeta'],
  data: function() {
    // template的编译似乎没有等data完成， data async未结束，模板编译完成
    let selectDatas = {};
    for(let i= 0,len=this.formMeta.length; i<len; i++) {
      let item = this.formMeta[i];
      if ('q-select' == item.xType) {
        selectDatas[item.name] = [];
      }
    }

    return {
      loading: true,
      selectDatas: selectDatas
    };
  },
  computed: {
    open:function() {return this.showDialog;},
    formDatas: function() {
      return this.formData;
    }
  },
  mounted: async function() {
    //获取select 中的选项数据
    let selectDatas = {};
    for(let i= 0,len=this.formMeta.length; i<len; i++) {
      let item = this.formMeta[i];
      if('q-select'==item.xType) {
        let dsBind = item.bind;
        //创建datasource 获取数据
        this.ds = new DataSource(DataCenter.getDSConfig(dsBind));

        let response = await this.ds.paged();
        let data = await response.json();
        if(data.success) {
          selectDatas[item.name] = data.result;
        }
      }
    }
    this.selectDatas = selectDatas;
  },
  methods: {
    handleSave: function() {
      this.$emit('handleItemChange', this.formDatas, this);
    },
    cancel: function() {
      //props变量不允许直接双向绑定，使用计算属性，如果不改变父组件的showDialog值，则不会再次触发对话框 打开
      this.$emit('update:showDialog', false)
    }
  }
}
</script>




